body {
  letter-spacing: .08rem;
  height: 100vh;
  background: url("look.com.ua_315924.86b1f59e.jpg") center / cover no-repeat;
  flex-direction: column;
  display: flex;
}

form {
  align-items: flex-end;
  margin-top: 60px;
  display: flex;
}

input, button, a {
  border-radius: 4px;
  padding: .2em .8em;
  font-size: 1.2rem;
  transition: all .3s;
}

input {
  height: 40px;
  width: 180px;
  border: none;
  margin-right: 10px;
  display: block;
}

button {
  height: 40px;
  cursor: pointer;
  border: none;
}

a {
  color: #fff;
  background-color: #000;
  margin: .5rem;
  font-size: 1rem;
  text-decoration: none;
  position: absolute;
  top: 0;
  left: 0;
}

button:hover, a:hover {
  color: #fff;
  background-color: #e9a812;
}

